@font-face {
  font-family: "Yeseva One";
  src: url("../fonts/YesevaOne-Regular.woff2") format("woff2"), url("../fonts/YesevaOne-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "FuturaDemiC";
  src: url("../fonts/FuturaDemiC.woff2") format("woff2"), url("../fonts/FuturaDemiC.woff") format("woff");
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "FuturaMediumC";
  src: url("../fonts/FuturaMediumC.woff2") format("woff2"), url("../fonts/FuturaMediumC.woff") format("woff");
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "FuturaBookC";
  src: url("../fonts/FuturaBookC.woff2") format("woff2"), url("../fonts/FuturaBookC.woff") format("woff");
  font-style: normal;
  font-display: swap; }

body {
  font-size: 14px;
  line-height: 26px;
  margin: 0;
  padding: 0;
  font-family: "FuturaBookC", "Arial", sans-serif;
  background-color: #181818;
  color: #ffffff; }

img {
  vertical-align: bottom; }

