.page-header {
  width: 375px; }

@media (min-width: 1438px) {
  .page-header {
    width: 1438px; }
  .page-header__logo {
    font-family: FuturaDemiC;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    color: #E3B873; } }

.site-list {
  display: none; }

.site-list__link {
  font-size: 14px;
  line-height: 16px;
  color: #FFFFFF;
  text-decoration: none; }

@media (min-width: 1438px) {
  .site-list {
    display: flex;
    justify-content: space-between;
    width: 550px; } }

.main-nav {
  align-items: center; }

.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0; }

@media (min-width: 1438px) {
  .main-nav {
    display: flex;
    justify-content: space-between;
    padding-left: 80px;
    padding-right: 100px;
    padding-top: 24px;
    padding-bottom: 22px; } }

.contacts-list__number,
.contacts-list__btn {
  text-decoration: none; }

.contacts-list__btn {
  padding: 13px 30px; }

.contacts-list__feedback--none {
  display: none; }

@media (min-width: 1438px) {
  .contacts-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 320px; }
  .contacts-list__number {
    font-family: FuturaDemiC;
    font-size: 14px;
    line-height: 16px;
    text-align: right;
    text-decoration-line: underline;
    color: #E3B873; }
  .contacts-list__feedback--none {
    display: inline-block; } }

.wrapper {
  width: 375px; }

@media (min-width: 1438px) {
  .wrapper {
    width: 1438px;
    margin: 0 auto; } }

.btn {
  display: inline-block;
  font-family: FuturaDemiC;
  color: #ffffff;
  background: #E3B873;
  box-shadow: 0px 5px 15px rgba(227, 184, 115, 0.2);
  border-radius: 2px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none; }
